<script lang="ts">
    import TopNavigation from "../top/TopNavigation.svelte";
    import {Icon, LockClosed, Refresh} from "svelte-hero-icons";
    import {onMount} from "svelte";
    import {currentError} from "../../js/Store";
    import {ErrorIds} from "../../js/enums/ErrorIds";

    onMount(() => {
        currentError.subscribe(value => {
            if(value != undefined) {
                if(value.id == ErrorIds.create_session) {
                    siteState = false;
                }
            }
        })
    });

    export let submitCallback;
    export let changeToRegisterCallback;

    let siteState = false;
</script>

<div class='content-container'>
    <TopNavigation title={"Login"}/>
    <div class='content-list'>
        <div class="mt-9 flex justify-center py-12 px-4 sm:px-6 lg:px-8">
            <div class="max-w-md w-full space-y-8">
                <div>
                    <img class="mx-auto h-24 w-24" src="/images/logo512.png" alt="Workflow"/>
                    <h2 class="text-center text-3xl font-extrabold text-gray-900 dark:text-gray-300">Login to your account</h2>
                    <p class="mt-2 text-center text-sm text-gray-600 dark:text-gray-400">Or{' '}<span class="cursor-pointer font-medium dark:text-indigo-400 text-indigo-600 hover:text-indigo-500" on:click={function() {
                      changeToRegisterCallback();
                    }}>create one</span></p>
                </div>
                <form class="mt-8 space-y-6" action="#" method="POST" on:submit={function(event) {
                    event.preventDefault();

                    if(!siteState) {
                        submitCallback(document.getElementById("loginUsernameInput").value, document.getElementById("loginPasswordInput").value, document.getElementById("remember-me").checked);

                        siteState = true;
                    }
                }}>
                    <input type="hidden" name="remember" defaultValue="true" />
                    <div class="rounded-md shadow-sm -space-y-px">
                        <div>
                            <input id="loginUsernameInput" name="loginUsernameInput" type="text" autoComplete="text" required class="appearance-none rounded-none relative block w-full px-3 py-2 border bg-gray-white dark:bg-gray-800 dark:border-gray-700 border-gray-300 dark:placeholder-gray-600 placeholder-gray-400 dark:text-gray-300 text-gray-500 rounded-t-md focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm" placeholder="Username"/>
                        </div>
                        <div>
                            <input id="loginPasswordInput" name="loginPasswordInput" type="password" autoComplete="current-password" required class="appearance-none rounded-none relative block w-full px-3 py-2 border bg-gray-white dark:bg-gray-800 dark:border-gray-700 border-gray-300 dark:placeholder-gray-600 placeholder-gray-400 dark:text-gray-300 text-gray-500 rounded-b-md focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm" placeholder="Password"/>
                        </div>
                    </div>

                    <div class="flex items-center justify-between">
                        <div class="flex items-center">
                            <input id="remember-me" name="remember-me" type="checkbox" class="h-4 w-4 text-indigo-600 bg-white dark:bg-gray-500 focus:ring-indigo-500 border-gray-300 rounded"/>
                            <label for="remember-me" class="ml-2 block text-sm dark:text-gray-300 text-gray-500">
                                Remember me
                            </label>
                        </div>

                        <div class="text-sm">
                            <span class="cursor-pointer font-medium dark:text-indigo-400 text-indigo-600 hover:text-indigo-500">Forgot your password?</span>
                        </div>
                    </div>

                    <div>
                        <button type="submit" class="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
                            <span class="absolute left-0 inset-y-0 flex items-center pl-3">
                                {#if siteState}
                                    <Icon src={Refresh} size="19" class="h-5 w-5 text-indigo-500 group-hover:text-indigo-400 service-button-loading" aria-hidden="true" />
                                {:else}
                                    <Icon src={LockClosed} size="19" class="h-5 w-5 text-indigo-500 group-hover:text-indigo-400" aria-hidden="true" />
                                {/if}
                            </span>Login
                        </button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>